<template>
  <div class="goods-detail-container" :style='{"overflow":isSlide}' @scroll="scroll">
    <Swiper :swiperImgs="swiperImgs" :swiperOptions="swiperOptions" :height="swiperHeight" @touchstart="previewImg"></Swiper>
    <div class="goods-price-title">
      <div class="goods-price">
        <div class="sell-price">${{goodsDetail.sell_price}}</div>
        <div class="market-price">${{goodsDetail.market_price}}</div>
      </div>
      <div class="goods-title">{{goodsDetail.title}}</div>
    </div>
    <div class="delivery-info">
      <div class="text">Deliver:</div>
      <div class="address">
        <i class="fa fa-map-marker" aria-hidden="true"></i> Guangzhou
      </div>
      <div class="cost">
        <span>Express：</span>$0.00
      </div>
      <div class="monthly-sales">Monthly Sales {{goodsDetail.month_sell_count}}</div>
    </div>
    <div class="join-cart">
      
    </div>
    <div class="goods-detail">
      <div class="goods-detail-title">
        <i class="fa fa-long-arrow-left" aria-hidden="true"></i>Goods Detail
        <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
      </div>
      <div class="goods-detail-item" v-for="(item,index) in goodsDetail.detail_desc" :key="index">
        <div class="goods-detail-brief">{{item.detail_desc_title}}</div>
        <img
          :src="item.detail_desc_img_src" @click="previewImg(item.detail_desc_img_src)"
        />
      </div>
      
      <div class="price-explain">
        <div class="price-explain-title">Price Explaination</div>
        <div class="scribing-price">
          <div><span>*</span>Scribing Price</div>
          <p>The counter price, tag price, genuine retail price, manufacturer's guide price or the sales price once displayed of the commodity are not the original price, but are for reference only.</p>
        </div>
        <div class="no-scribing-price">
           <div><span>*</span>No Scribing Price</div>
           <p>The real-time price of goods shall not change its nature due to the difference of expression. The specific transaction price shall be subject to the settlement price on the order page according to the change of participating activities or members' use of preferential bonds and points.</p>
        </div>
      </div>
    </div>
    <div class="image-preview" v-if="isPreviewImg"  @click="closePreviewImg" :style="{'top':previewImgTop}">
      <img :src="previewImgSrc">
    </div>  
  </div>
</template>

<script>
import Swiper from "../common/Swiper";
export default {
  data() {
    return {
      goodsDetail: {},
      swiperImgs: [],
      swiperHeight: "8rem",
      swiperOptions: {
        pagination: {
          el: ".swiper-pagination"
        }
      },
      isPreviewImg:false,
      previewImgSrc:'',
      isSlide:"auto",
      previewImgTop:0
    };
  },
  created() {
    this.getGoodsDetailData();
  },
  methods: {
    async getGoodsDetailData() {
      console.log(this.$route.params.id);
      const { data } = await this.$http.post(
        this.baseUrl + "/api/goods/goodsdetail",
        { id: this.$route.params.id }
      );
      
      this.goodsDetail=data.data
      this.swiperImgs = this.goodsDetail.goods_imgs.map(item => {
        return item.img_src;
      });
      console.log(this.goodsDetail);
    },
    previewImg(url){
      // console.log(url)
      this.isPreviewImg=true
      this.previewImgSrc=url
      this.isSlide='hidden'
      
    },
    closePreviewImg(){
      this.isPreviewImg=false
      this.previewImgSrc=''
      this.isSlide='auto'
    },
    scroll(e){
      // console.log(e.target.scrollTop)
      this.previewImgTop=e.target.scrollTop+"px"
    }
  },
  components: {
    Swiper
  }
};
</script>

<style scoped lang="scss">
$text-color: #999;
.goods-price-title {
  padding: 15px;
  border: 1px solid #999;
  background-color: #eee;
  margin-top: 20px;
  box-shadow: 1px 1px 5px #999;
  .goods-price {
    display: flex;
    line-height: 1rem;
    height: 1rem;
    .sell-price {
      font-size: 0.4rem;
      color: #f40;
    }
    .market-price {
      font-size: 0.3rem;
      color: #999;
      text-decoration: line-through;
      margin-left: 20px;
    }
  }
  .goods-title {
    font-size: 0.35rem;
    text-align: left;

    // word-wrap: none;
    word-break: break-all;
    hyphens: auto;
  }
}
.delivery-info {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #eee;
  margin: 20px 0;
  box-shadow: 1px 1px 5px #999;
  border: 1px solid #999;
  background-color: #eee;
  font-size: 0.35rem;
  padding: 20px;
  .text {
    color: $text-color;
    margin-right: 20px;
  }
  .address {
    flex: 1;
    text-align: left;
    i {
      color: $text-color;
    }
  }
  .cost {
    span {
      color: $text-color;
    }
  }
  .monthly-sales {
    color: $text-color;
    font-size: 0.3rem;
    margin-left: 50px;
  }
}
.goods-detail {
  background-color: #000;
  padding-bottom: 20px;
  .goods-detail-title {
    line-height: 1rem;
    font-size: 0.4rem;
    color: #555;
    background-color: #eee;
    i {
      color: $text-color;
    }

    i:nth-of-type(1) {
      margin-right: 20px;
    }
    i:nth-of-type(2) {
      margin-left: 20px;
    }
  }
  .goods-detail-item {
    height: 6rem;
    display: flex;
    margin: 10px 0;
    background-color: #aaa;
    .goods-detail-brief {
      display: flex;
      margin: 0 10px;
      align-items: center;
      font-size: 0.5rem;  
      width: 25%;
    }
    img {
      flex: 1;
      margin: 10px;
    }
  }
  .goods-detail-item:nth-of-type(odd) {
    flex-direction: row-reverse;
  }
  .price-explain{
    margin-top: 20px;
    background-color: #eee;
    .price-explain-title{
      font-size: 0.5rem;
      line-height: 1rem;
      border-bottom: 1px solid #ccc;
      text-align: left;
      padding-left: 20px;
      color: #555;
    }
    .scribing-price,.no-scribing-price{
      margin-top:10px;
      font-size: 0.2rem;
      text-align: left;
      padding: 10px;
      color: #555;
      div{
        color: #f40;
        
        align-items: center;
        span{
          margin-right: 10px;
        }
      }
      p{
        text-indent: 0.4rem;
        word-wrap: none;
        word-break: break-all;
        hyphens: auto;
      }
    }
    
  }
}
.image-preview{
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  position: absolute;
  width: 100%;
  z-index: 9;
  display: flex;
  justify-content: center;
  align-items: center;
  img{
    width: 100%;
    margin:0 10px;
  }
}
.goods-detail-container{
  height: 100%;
  position: relative;
}
</style>